

import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { HomePage } from '../home/home';

//import { ListanoticiasPage } from '../../noticia/listanoticias/listanoticias'
import { LoginPage } from '../../login/login'
import { SingletonService } from "../../../providers/util/singleton.service" 

import * as firebase from 'firebase/app';


@IonicPage()
@Component({
  selector: 'page-room',
  templateUrl: 'room.html',
})
export class RoomPage {

  rooms = [];

  ref = firebase.database().ref('chatrooms/');



  constructor(public navCtrl: NavController, public navParams: NavParams, public singleton:SingletonService) {
    
    if (this.singleton.loginemail.length==0){
      this.navCtrl.setRoot(LoginPage)
    }
    
    
   

    
  }



  joinRoom(key:any) {
    this.navCtrl.setRoot(HomePage, {
      key: key,
      nickname: this.singleton.loginname
    });
  }

 exitRooms() {
  
    this.navCtrl.setRoot('ListanoticiasPage');
  }




  ionViewDidLoad() {
    console.log('ionViewDidLoad RoomPage');
    this.ref.on('value', resp => {
      this.rooms = [];
      console.log(snapshotToArray(resp));
      this.rooms = snapshotToArray(resp);
    });


  }

public showmychatsonly(array:any){
  return array.filter(
  (obj:any)=>{ 
      return obj.email === this.singleton.loginemail;
  }); 
}

}

export const snapshotToArray = snapshot => {
  let returnArr = [];

  snapshot.forEach(childSnapshot => {
    let item = childSnapshot.val();
    item.key = childSnapshot.key;
    returnArr.push(item);
  });

  return returnArr;
};


